<script lang="typescript">import { onMount } from "svelte";
;
export let typewriter;
export let onNext = () => { };
class Fragment {
    constructor(text, type) {
        this.text = '';
        this.type = 'preview';
        this.text = text;
        this.type = type;
    }
}
let fragments = [];
onMount(() => {
    typewriter.attachRenderListener(render);
    render();
});
function render() {
    fragments = [];
    const words = typewriter.getWords();
    for (let wordIndex = 0; wordIndex < words.length; wordIndex++) {
        const word = words[wordIndex];
        const isPreviewWord = wordIndex > typewriter.getWordIndex();
        for (let charIndex = 0; charIndex < word.text.length; charIndex++) {
            let char = word.text[charIndex];
            const isTypo = word.typos.includes(charIndex);
            const isPreview = isPreviewWord || (wordIndex === typewriter.getWordIndex() && charIndex >= typewriter.getCharIndex());
            const fragmentType = isPreview ? 'preview' : isTypo ? 'error' : 'normal';
            // Display space typos as underscores
            if (fragmentType === 'error' && char === ' ') {
                char = '_';
            }
            if (fragments.length > 0 && fragments[fragments.length - 1].type === fragmentType) {
                fragments[fragments.length - 1].text += char;
            }
            else {
                const fragment = new Fragment(char, fragmentType);
                fragments.push(fragment);
            }
        }
    }
}
function onKeypress(e) {
    if (e.key === 'Enter' && typewriter.hasEnded) {
        onNext();
    }
    typewriter.write(e.key);
}
function onKeydown(e) {
    if (e.key === 'Backspace') {
        typewriter.undo();
    }
}
</script>


    <div class="wrapper">    
        <div
            class="typewriter"
            tabindex="0"
            on:keypress={onKeypress}
            on:keydown={onKeydown}
        >
            {#each fragments as fragment}
                <span
                    class={fragment.type + ' fragment'}
                >{fragment.text}</span>
            {/each}
        </div>
    </div>


<style lang="scss">.wrapper {
  position: relative;
  font-size: 32px;
  font-family: 'Source Code Pro', monospace;
  font-weight: 400;
  line-height: 1.3em;
  max-height: calc(1.3em * 3.8);
  overflow: hidden; }
  .wrapper::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: calc(1.3em);
    background: linear-gradient(0deg, #2D2D2D 0%, rgba(45, 45, 45, 0) 100%);
    pointer-events: none; }

.typewriter {
  width: 50ch;
  height: calc(1.3em * 3.8);
  overflow: hidden; }
  .typewriter span.preview {
    opacity: .4; }
  .typewriter span.error {
    color: #C24646; }
  .typewriter:focus {
    outline: none; }
</style>